'use client';

import React, { useEffect, useState } from 'react';
import {
    BarChart3,
    TrendingUp,
    TrendingDown,
    ShoppingCart,
    DollarSign,
    Package,
    Users,
    Calendar,
    RefreshCw,
    Loader2
} from 'lucide-react';

interface StatsData {
    orders: {
        today: number;
        thisWeek: number;
        thisMonth: number;
        total: number;
        pending: number;
        completed: number;
        cancelled: number;
    };
    revenue: {
        today: number;
        thisWeek: number;
        thisMonth: number;
        total: number;
    };
    products: {
        total: number;
        active: number;
    };
    topProducts: Array<{
        name: string;
        quantity: number;
        revenue: number;
    }>;
    recentActivity: Array<{
        id: string;
        type: string;
        description: string;
        time: string;
    }>;
}

export default function AdminStatsPage() {
    const [stats, setStats] = useState<StatsData | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState('');
    const [period, setPeriod] = useState<'today' | 'week' | 'month' | 'all'>('month');

    const fetchStats = async () => {
        setIsLoading(true);
        try {
            // Fetch dashboard overview
            const response = await fetch('http://localhost:3333/api/admin/dashboard', {
                headers: {
                    'X-Admin-Id': JSON.parse(localStorage.getItem('admin_data') || '{}').id
                }
            });

            if (!response.ok) throw new Error('Erreur de chargement');

            const data = await response.json();

            // Fetch additional stats from orders
            const ordersRes = await fetch('http://localhost:3333/api/admin/orders?limit=1000', {
                headers: {
                    'X-Admin-Id': JSON.parse(localStorage.getItem('admin_data') || '{}').id
                }
            });
            const ordersData = await ordersRes.json();

            // Calculate additional stats from orders
            const now = new Date();
            const startOfWeek = new Date(now);
            startOfWeek.setDate(now.getDate() - now.getDay());
            startOfWeek.setHours(0, 0, 0, 0);

            const startOfMonth = new Date(now.getFullYear(), now.getMonth(), 1);

            const orders = ordersData.data || [];

            const weekOrders = orders.filter((o: any) => new Date(o.created_at) >= startOfWeek);
            const monthOrders = orders.filter((o: any) => new Date(o.created_at) >= startOfMonth);
            const completedOrders = orders.filter((o: any) => o.status === 'completed');
            const cancelledOrders = orders.filter((o: any) => o.status === 'cancelled');

            const weekRevenue = weekOrders
                .filter((o: any) => o.status === 'completed')
                .reduce((sum: number, o: any) => sum + (o.total || 0), 0);

            const monthRevenue = monthOrders
                .filter((o: any) => o.status === 'completed')
                .reduce((sum: number, o: any) => sum + (o.total || 0), 0);

            // Fetch products
            let products: any[] = [];
            try {
                const productsRes = await fetch('http://localhost:3333/api/admin/products', {
                    headers: {
                        'X-Admin-Id': JSON.parse(localStorage.getItem('admin_data') || '{}').id
                    }
                });
                const productsData = await productsRes.json();
                // Handle different response formats
                products = Array.isArray(productsData) ? productsData :
                    Array.isArray(productsData?.data) ? productsData.data : [];
            } catch (e) {
                console.error('Error fetching products:', e);
            }

            // Calculate top products from orders
            const productCounts: Record<string, { name: string; quantity: number; revenue: number }> = {};
            orders.forEach((order: any) => {
                const items = typeof order.items === 'string' ? JSON.parse(order.items) : order.items;
                if (Array.isArray(items)) {
                    items.forEach((item: any) => {
                        const key = item.slug || item.name;
                        if (!productCounts[key]) {
                            productCounts[key] = { name: item.name, quantity: 0, revenue: 0 };
                        }
                        productCounts[key].quantity += item.quantity || 1;
                        productCounts[key].revenue += (item.price || 0) * (item.quantity || 1);
                    });
                }
            });

            const topProducts = Object.values(productCounts)
                .sort((a, b) => b.revenue - a.revenue)
                .slice(0, 5);

            setStats({
                orders: {
                    today: data.orders?.today || 0,
                    thisWeek: weekOrders.length,
                    thisMonth: monthOrders.length,
                    total: data.orders?.total || orders.length,
                    pending: data.orders?.pending || 0,
                    completed: completedOrders.length,
                    cancelled: cancelledOrders.length
                },
                revenue: {
                    today: data.revenue?.today || 0,
                    thisWeek: weekRevenue,
                    thisMonth: monthRevenue,
                    total: data.revenue?.total || 0
                },
                products: {
                    total: products.length,
                    active: products.filter((p: any) => p.is_active).length
                },
                topProducts,
                recentActivity: (data.recentOrders || []).slice(0, 5).map((o: any) => ({
                    id: o.id,
                    type: 'order',
                    description: `Commande #${o.id?.substring(0, 8)} - ${o.customer_name}`,
                    time: new Date(o.created_at).toLocaleString('fr-FR')
                }))
            });
        } catch (err: any) {
            setError(err.message);
        } finally {
            setIsLoading(false);
        }
    };

    useEffect(() => {
        fetchStats();
    }, []);

    const formatCurrency = (amount: number) => {
        return new Intl.NumberFormat('fr-FR', {
            style: 'decimal',
            minimumFractionDigits: 0,
            maximumFractionDigits: 0
        }).format(amount) + ' FCFA';
    };

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-[400px]">
                <Loader2 className="w-8 h-8 animate-spin text-gray-400" />
            </div>
        );
    }

    if (error) {
        return (
            <div className="bg-red-50 border border-red-200 rounded-xl p-6 text-center">
                <p className="text-red-600">{error}</p>
                <button
                    onClick={fetchStats}
                    className="mt-4 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700"
                >
                    Réessayer
                </button>
            </div>
        );
    }

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Statistiques</h1>
                    <p className="text-gray-500">Vue d'ensemble des performances</p>
                </div>
                <button
                    onClick={fetchStats}
                    className="flex items-center gap-2 px-4 py-2 bg-black text-white rounded-xl hover:bg-gray-800 transition-colors"
                >
                    <RefreshCw className="w-4 h-4" />
                    Actualiser
                </button>
            </div>

            {/* Period Selector */}
            <div className="flex gap-2">
                {(['today', 'week', 'month', 'all'] as const).map((p) => (
                    <button
                        key={p}
                        onClick={() => setPeriod(p)}
                        className={`px-4 py-2 rounded-lg font-medium transition-colors ${period === p
                            ? 'bg-black text-white'
                            : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                            }`}
                    >
                        {p === 'today' ? "Aujourd'hui" : p === 'week' ? 'Cette semaine' : p === 'month' ? 'Ce mois' : 'Tout'}
                    </button>
                ))}
            </div>

            {/* Main Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                {/* Revenue Card */}
                <div className="bg-gradient-to-br from-green-500 to-emerald-600 rounded-2xl p-6 text-white">
                    <div className="flex items-center justify-between mb-4">
                        <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center">
                            <DollarSign className="w-6 h-6" />
                        </div>
                        <TrendingUp className="w-5 h-5 text-green-200" />
                    </div>
                    <p className="text-green-100 text-sm">Revenus {period === 'today' ? "aujourd'hui" : period === 'week' ? 'cette semaine' : period === 'month' ? 'ce mois' : 'totaux'}</p>
                    <p className="text-2xl font-bold mt-1">
                        {formatCurrency(
                            period === 'today' ? stats?.revenue.today || 0 :
                                period === 'week' ? stats?.revenue.thisWeek || 0 :
                                    period === 'month' ? stats?.revenue.thisMonth || 0 :
                                        stats?.revenue.total || 0
                        )}
                    </p>
                </div>

                {/* Orders Card */}
                <div className="bg-gradient-to-br from-blue-500 to-indigo-600 rounded-2xl p-6 text-white">
                    <div className="flex items-center justify-between mb-4">
                        <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center">
                            <ShoppingCart className="w-6 h-6" />
                        </div>
                        <span className="text-blue-200 text-sm font-medium">
                            {stats?.orders.pending || 0} en attente
                        </span>
                    </div>
                    <p className="text-blue-100 text-sm">Commandes {period === 'today' ? "aujourd'hui" : period === 'week' ? 'cette semaine' : period === 'month' ? 'ce mois' : 'totales'}</p>
                    <p className="text-2xl font-bold mt-1">
                        {period === 'today' ? stats?.orders.today :
                            period === 'week' ? stats?.orders.thisWeek :
                                period === 'month' ? stats?.orders.thisMonth :
                                    stats?.orders.total}
                    </p>
                </div>

                {/* Products Card */}
                <div className="bg-gradient-to-br from-purple-500 to-violet-600 rounded-2xl p-6 text-white">
                    <div className="flex items-center justify-between mb-4">
                        <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center">
                            <Package className="w-6 h-6" />
                        </div>
                    </div>
                    <p className="text-purple-100 text-sm">Produits actifs</p>
                    <p className="text-2xl font-bold mt-1">
                        {stats?.products.active} / {stats?.products.total}
                    </p>
                </div>

                {/* Completion Rate Card */}
                <div className="bg-gradient-to-br from-orange-500 to-amber-600 rounded-2xl p-6 text-white">
                    <div className="flex items-center justify-between mb-4">
                        <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center">
                            <BarChart3 className="w-6 h-6" />
                        </div>
                    </div>
                    <p className="text-orange-100 text-sm">Taux de complétion</p>
                    <p className="text-2xl font-bold mt-1">
                        {stats?.orders.total ? Math.round((stats.orders.completed / stats.orders.total) * 100) : 0}%
                    </p>
                </div>
            </div>

            {/* Secondary Stats */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Order Status Breakdown */}
                <div className="bg-white rounded-2xl border border-gray-200 p-6">
                    <h3 className="text-lg font-bold text-gray-900 mb-4">Répartition des commandes</h3>
                    <div className="space-y-4">
                        <div className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                                <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                                <span className="text-gray-600">En attente</span>
                            </div>
                            <span className="font-bold text-gray-900">{stats?.orders.pending || 0}</span>
                        </div>
                        <div className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                                <div className="w-3 h-3 rounded-full bg-green-500"></div>
                                <span className="text-gray-600">Complétées</span>
                            </div>
                            <span className="font-bold text-gray-900">{stats?.orders.completed || 0}</span>
                        </div>
                        <div className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                                <div className="w-3 h-3 rounded-full bg-red-500"></div>
                                <span className="text-gray-600">Annulées</span>
                            </div>
                            <span className="font-bold text-gray-900">{stats?.orders.cancelled || 0}</span>
                        </div>
                    </div>

                    {/* Visual bar */}
                    <div className="mt-6 h-4 bg-gray-100 rounded-full overflow-hidden flex">
                        {stats?.orders.total && stats.orders.total > 0 && (
                            <>
                                <div
                                    className="bg-green-500 h-full"
                                    style={{ width: `${(stats.orders.completed / stats.orders.total) * 100}%` }}
                                ></div>
                                <div
                                    className="bg-yellow-500 h-full"
                                    style={{ width: `${(stats.orders.pending / stats.orders.total) * 100}%` }}
                                ></div>
                                <div
                                    className="bg-red-500 h-full"
                                    style={{ width: `${(stats.orders.cancelled / stats.orders.total) * 100}%` }}
                                ></div>
                            </>
                        )}
                    </div>
                </div>

                {/* Top Products */}
                <div className="bg-white rounded-2xl border border-gray-200 p-6">
                    <h3 className="text-lg font-bold text-gray-900 mb-4">Produits les plus vendus</h3>
                    {stats?.topProducts && stats.topProducts.length > 0 ? (
                        <div className="space-y-4">
                            {stats.topProducts.map((product, index) => (
                                <div key={index} className="flex items-center justify-between">
                                    <div className="flex items-center gap-3">
                                        <span className="w-6 h-6 rounded-full bg-gray-100 flex items-center justify-center text-sm font-bold text-gray-600">
                                            {index + 1}
                                        </span>
                                        <div>
                                            <p className="font-medium text-gray-900">{product.name}</p>
                                            <p className="text-sm text-gray-500">{product.quantity} vendus</p>
                                        </div>
                                    </div>
                                    <span className="font-bold text-green-600">{formatCurrency(product.revenue)}</span>
                                </div>
                            ))}
                        </div>
                    ) : (
                        <p className="text-gray-400 text-center py-8">Aucune donnée disponible</p>
                    )}
                </div>
            </div>

            {/* Recent Activity */}
            <div className="bg-white rounded-2xl border border-gray-200 p-6">
                <h3 className="text-lg font-bold text-gray-900 mb-4">Activité récente</h3>
                {stats?.recentActivity && stats.recentActivity.length > 0 ? (
                    <div className="space-y-3">
                        {stats.recentActivity.map((activity, index) => (
                            <div key={index} className="flex items-center justify-between py-3 border-b border-gray-100 last:border-0">
                                <div className="flex items-center gap-3">
                                    <div className="w-10 h-10 rounded-full bg-blue-100 flex items-center justify-center">
                                        <ShoppingCart className="w-5 h-5 text-blue-600" />
                                    </div>
                                    <p className="text-gray-900">{activity.description}</p>
                                </div>
                                <span className="text-sm text-gray-500">{activity.time}</span>
                            </div>
                        ))}
                    </div>
                ) : (
                    <p className="text-gray-400 text-center py-8">Aucune activité récente</p>
                )}
            </div>
        </div>
    );
}
